/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.html-button {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  display: flex;
  padding: 16px;
}
.html-button:focus {
  outline: 0;
  box-shadow: none;
}
.html-button:visited, .html-button:active, .html-button:focus, .html-button:hover {
  cursor: pointer;
}
.html-button ._icon {
  transition: all 100ms ease-out;
}
.html-button._expanded ._icon:first-child {
  margin-right: 16px;
}