/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
/**
 * @param {number} $size - Size in pixels (without units). Default: 2
 * @param {hex} $color - Any hex color
 * @param {number} $correction - Corrects rounding and reduces text-shadows. For better understanding see this CodePen. Default: 0
 */
/** unused */
/**
 * * Color mixin to be used like:
 * * +C(color, primary)
 * * +C(background, secondary)
 * */
@font-face {
  font-family: "VAL";
  src: url("/VAL.otf");
}
/**
 * * A fix for a bug when you translate elements inside a parent that has:
 * *   `overflow: hidden; border-radius: x`
 * * This fix is to be applied to the parent element.
 * * (Bug: during translate animation, the parent's border-radius becomes 0 for a split second)
 * */
.icon-info {
  background-color: var(--q-color-white, );
  min-width: 280px;
  width: 400px;
  max-width: inherit;
  position: relative;
  overflow: hidden;
  border-radius: 1rem;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
}

.dark-mode .icon-info {
  background-color: #1D1D1D;
  background-color: var(--q-color-nightfall, #1D1D1D);
}

._toggle-code-button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}

._top-door, ._bottom-door {
  z-index: 1;
  position: relative;
  background-color: inherit;
  transition: all 200ms ease-out !important;
}

._top-door {
  height: 288px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
@media screen and (min-width: 360px) {
  ._top-door {
    height: 320px;
  }
}

._bottom-door {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 162px;
}
@media screen and (min-width: 360px) {
  ._bottom-door {
    height: 180px;
  }
}
._bottom-door::before {
  content: "";
  background-color: rgba(190, 195, 214, 0.1);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

._top-door-transform {
  transform: translateY(-100%);
}

._bottom-door-transform {
  transform: translateY(100%);
}

._code-section {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 0;
}

._tab {
  height: 50px;
  padding-top: 8px;
}
._tab > * {
  font-family: "Montserrat", sans-serif;
  transition: color 500ms;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

._tab-panels {
  height: calc(100% - 100px);
}
._tab-panels > * {
  height: 100%;
}
._tab-panels ._tab-panel {
  padding: 0;
  height: inherit;
}
._tab-panels ._code-block {
  height: inherit;
}
._tab-panels ._code-block > *, ._tab-panels ._code-block > * > * {
  height: inherit;
}
._tab-panels ._code-block code {
  white-space: pre-wrap;
}